// 一口价商品
let _this = this
import config from '@/api/config.js';
import {request} from '@/api/reques.js'
import {goWindow} from '@/api/navigate.js'  // 页面跳转
export default{
	data(){
		return{
			goodsList:[],
			page_index:1,
			page_count:0,
			addressName:this.$store.state.city
		}
	},
	props:{
		options:{
			type:Object,
			default:()=>{
				return {}
			}
		},
		num:{
			type:String
		},
		carId:{
			type:String
		},
		brandId:{
			type:String
		},
		priceIndex:{
			type:Number
		},
		min:{
			type:String
		},
		max:{
			type:String
		},
		gather:{
			type:Boolean
		},
		pfId:{
			type:String
		},
		qdId:{
			type:String
		},
		min_ml:{
			type:String
		},
		max_ml:{
			type:String
		}
	},
	watch:{
		num(num){
			_this.getGoodsList()
		},
		carId(id){
			_this.getGoodsList()
		},
		brandId(){
			_this.getGoodsList()
		},
		priceIndex(id){
			_this.getGoodsList();
			id = "";
		},
		gather(bool){
			if(bool){
				_this.getGoodsList();
				this.$emit("handleGather",false)
			}
		}
	},
	created() {
		this.getGoodsList()
		_this = this
	},
	filters:{
		filterImg(src){
			if(src){
				return config.domain + src
			}
		}
	},
	methods:{
		// 页面跳转
		handlePric(goodsId){
			goWindow('/pages/general/details?id=',goodsId)
		},
		// 获取商品列表
		getGoodsList(page_index = 1){
			this.$store.commit("changeGroup",6) 
			uni.showLoading({
				title: '加载中'
			});
			request("/index/getUsedCarGoodsList",{
				group_id:6,
				page_index:page_index,
				page_size:10,
				ai_sort:this.num,
				car_type:this.carId,
				brands_id:this.brandId,
				min_price:this.min,
				max_price:this.max,
				pf_id:this.pfId,
				qd_id:this.qdId,
				min_ml:this.min_ml,
				max_ml:this.max_ml,
				city_name:this.addressName,
			},"GET")
			.then((res)=>{
				let data = res.data.data.data
				if(page_index > 1){
					data = this.goodsList.concat(data)
				}
				this.goodsList = data;
				this.page_count = res.data.data.page_count;
				this.$emit("changePage",page_index,this.page_count)
				setTimeout(() => {
					uni.hideLoading();
				}, 500);
			})
		},
		handleErr(e,index){
			if(e.type=='error'){
				this.goodsList[index].pic_cover = "../../../static/images/list.jpg"
			}
			
		},
		handleAll(){
			this.addressName = "";
			this.num = "";
			this.carId = "";
			this.brandId = "";
			this.min = "";
			this.max = "";
			this.pfId = "";
			this.qdId = "";
			this.min_ml = "";
			this.max_ml = "";
			this.getGoodsList()
		}
	}
}
